import React from "react";
import { graphql, useStaticQuery, Link } from "gatsby";

type RelatedPostsProps = {
  category: string;
  currentSlug: string;
};

type PostNode = {
  node: {
    title: string;
    date: string;
    slug: string;
  };
};

const RelatedPosts: React.FC<RelatedPostsProps> = ({
  category,
  currentSlug,
}) => {
  const data = useStaticQuery(graphql`
    query RelatedPosts {
      allPost(limit: 5) {
        edges {
          node {
            title
            date(formatString: "YYYY-MM-DD")
            slug
          }
        }
      }
    }
  `);

  const posts = data.allPost.edges as PostNode[];

  if (!posts || !posts.length) {
    return <p>관련된 다른 포스트가 없습니다.</p>;
  }

  return (
    <div>
      <h2>다른 포스트 더 보기</h2>
      <ul>
        {posts.map(({ node }) => (
          <li key={node.slug}>
            <Link to={node.slug}>
              {node?.title} ({node?.date})
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default RelatedPosts;
